export interface HttpInfo {
  host?: string;
  request?: HttpProcessedRequest;
  response?: HttpProcessedResponse;
}

export type HttpRawRequest = {
  uri?: string;
  host?: string;
  body?: string;
  truncated: boolean;
  path?: string;
  headers?: Record<string, string>;
};

export type HttpProcessedRequest = {
  uri?: string;
  host?: string;
  body?: string;
  truncated: boolean;
  path?: string;
  headers?: string;
};

export type HttpRawResponse = {
  uri?: string;
  host?: string;
  statusCode?: number;
  body?: string;
  truncated: boolean;
  headers?: Record<string, string>;
};

export type HttpProcessedResponse = {
  uri?: string;
  host?: string;
  statusCode?: number;
  body?: string;
  truncated: boolean;
  headers?: string;
};

export type RequestProcessedData = {
  hasError?: boolean;
  uri?: string;
  host?: string;
  request?: HttpProcessedRequest;
  response?: HttpProcessedResponse;
};

export type RequestRawData = {
  hasError?: boolean;
  uri?: string;
  host?: string;
  request?: HttpRawRequest;
  response?: HttpRawResponse;
};
